.container{
    width: 100%;
    height: 80%;
    position: relative;
    z-index: 4;
}

.overlay{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 6;
}
.textContainer{
    width: 30%;
    text-align:left;
    transition: all 1s ease-in-out;
    position: absolute;
}

.textHeader{
    width: 100%;
    font-family:'Montserrat Alternates','sans-serif';
    font-weight: bold;
    font-size: 40px;
    color: #000;
}

.textBody{
    width: 100%;
    font-family:'open sans','sans-serif';
    font-weight: normal;
    font-size: 20px;
    color: #707070;
}